nav{
    display: flex;
    justify-content: center;
    padding: 1rem;
}
.nav_container{
    padding:1rem;
    position: fixed;
    background-color: rgba(0,0,0,0.3);
    display: flex;
    justify-content: space-between;
    width: 65vw;
    gap: 2rem;
    border-radius: 20px;
    z-index: 2;
}
.nav_container a{
    display: flex;
    font-size: 1.6rem;
    background: transparent;
    border-radius: 50%;
}
.nav_container a:hover{
    transform: scale(1.2, 1.2);
    background: var(--color-white);
    color: var(--color-bg);
    
}
.active{
    color: var(--color-white);
    
}

@media screen and (max-width:600px){
    .nav_container{
        gap: 1rem;
     }
    .nav_container a{
        font-size: 0.8rem;
    }
}