.footer{
    height: 60vh;
    width: 100vw;
    margin-top: 3rem;
    padding-top: 2rem;
    background: var(--color-primary);
    color: black;
    display: flex;
    justify-content: center;
}
.footer .menu .btn{
    border: 1px solid black;
    color: black;
    border-radius: 20px;
    padding: 0.7rem;
    font-size: 1rem;
}
.footer .socials a{
        color:black;
        transition:var(--transition);    
}
.footer .socials a:hover{
    color: var(--color-bg-variant);
}
.content{
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
}
.content h2{
    font-weight: 600;
    padding-bottom: 1rem;
}
.content h5{
    text-align: center;
    padding-bottom: 0.8rem;
}
.content .socials{
    font-size: 1.7rem;
    display: flex;
    gap: 2rem;
    padding-bottom: 0.8rem;
}
.content .menu{
    display: flex;
    gap: 1.5rem;
    padding-bottom: 1rem;
}
.content h5:nth-of-type(1){
    font-size: 1rem;
}
@media screen and (max-width:600px){
    .content{
        width: 100%;
        padding: 0 1rem;
    }
    .footer .menu .btn{
        border: 1px solid black;
        color: black;
        border-radius: 20px;
        padding: 0.7rem;
        font-size: 0.7rem;
    }
}