header{
    height: 100vh;
    padding-top: 8rem;
    overflow:hidden;
}
.header_container{
   text-align: center;
    height: 100%;
    position: relative;
    
}
.header_container h5{
    font-size: 20px;
}
.header_container div{
    display: flex;
    justify-content: center;
}
.header_container h1{
    font-size: 90px;
    transition: var(--transition);
    cursor: pointer;
    width: 50%;
}
.header_container h1:hover{
    text-shadow: 0px 0px 60px var( --color-primary);

}
.cv{
    margin-top: 2rem;
    display: flex;
    gap: 2rem;
    justify-content: center;
}

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8em;
    position: absolute;
    left:0;
    bottom: 3rem;
}
.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

.scroll_down{
    position:absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width:1024px){
header{
    height: 68vh;
}
}

@media screen and (max-width:600px){
    header{
        padding-top: 12rem;
    }
    .header_container h5{
        font-size: 10px;
    }
    .header_container h1{
        font-size: 50px;
    }
    .header_socials, .scroll_down{
        display: none;
    }
    .cv{
        font-size: 10px;
       gap: 1rem;
    }
}