.portfolio_title{
    padding-top: 5rem;
}
.portfolio_container{
    padding: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
    
}
.project_container{
    background: var(--color-bg-variant);
    height: 10%;
    width: 20%;
    border-radius: 20px;
    border: solid var(--color-primary);
    transition: var(--transition);
    
}
.project{
    position: relative;
}

.project .overlay{
    position: absolute;
    top:0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background: rgba(0,0,0, 0.6);
    opacity: 0;
    transition: opacity 0.21s;
}
.project .overlay--blur{
    backdrop-filter: blur(5px);
}
.project .overlay:hover{
    opacity: 1;  
}
.project .overlay h5{
    font-size: 1.3rem;
    font-weight: 500;
}
.project img{
    border-radius: 20px;
    aspect-ratio: 1/1;
}
.project .overlay a{
    margin-top: 1rem;
}
@media screen and (max-width:1024px) {
    .portfolio_container{
        flex-direction: row;
    }
    .project_container{
        height: 100%;
        width: 100%;
    }
}

@media screen and (max-width:600px) {
    .portfolio_container{
        flex-direction: column;
    }
    .project_container{
        height: 100%;
        width: 100%;
    }
   
    
}
 