.services_title{
    padding-top: 5.5rem;
}
.services_title h2{
    font-size: 2.5rem;
   
}
.services_container{
    display: flex;
    flex-direction: row;
    padding: 2rem;
    gap: 2rem;
}
.web{
    background-color: var(--color-bg-variant);
    padding: 1.5rem;
    border: solid var(--color-primary);
    border-radius: 20px;
}
.software{
    background-color: var(--color-bg-variant);
    padding: 1.5rem;
    border: solid var(--color-primary);
    border-radius: 20px;
}
.services_container span{
    display: flex;
    justify-content:center;
    font-size: 5rem;
    padding-bottom: 1rem;
}
.services_container h5{
    text-align: justify;
    font-size: 1rem;
    padding-bottom: 0.7rem;
}
.services_container a{
    font-size: 0.7rem;
    display: flex;
    justify-content: center;
 
}

@media screen and (max-width:600px){
    .services_container{
        flex-direction: column;
    }
    .web h2{
        font-size: 1.4rem;
    }
    .software h2{
        font-size: 1.4rem;
    }
}