.title_container{
    text-align: center;
    height: 30%;
    padding-top: 6rem;
}
.about_container h2{
    font-size: 2.5rem;
}
.section_content{
    display: flex;
    flex-direction: row;
    height: 70%;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
   
}
.mySelf{
    padding: 2rem;
    border-radius: 100px;
    cursor: pointer;
}
.mySelf img{
    height: 300px;
    width: 250px;
    border-radius: 40px;
    border: solid var(--color-primary);
    background-color: aqua;
}
.content_content{
    width: 38%;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    margin-left: 2rem;
    align-items: flex-start;

}
.aboutMySelf{
    display: flex;
    flex-direction: row;
    gap: 2rem;
}
.experience{
    background-color: var(--color-bg-variant);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
    cursor: pointer;
    transition: .3s ease-in;
}
.experience:hover{
   transform: scale(1.1, 1.1);
}
.experience a:nth-child(1){
    text-align: center;
    font-size: 1.5rem;
}
.experience h2{
    text-align: center;
}
.experience a:nth-child(2) h2 {
    color: var(--color-white);
}
.experience a h2:hover{
    color: var(--color-primary);
}
.experience h5{
    text-align: center;
}


.projects{
    background-color: var(--color-bg-variant);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
    transition: .3s ease-in;

}
.projects:hover{
    transform: scale(1.1, 1.1);
 }
 .projects a:nth-child(1){
    text-align: center;
    font-size: 1.5rem;
}
.projects h2{
    text-align: center;
}
.projects a:nth-child(2) h2 {
    color: var(--color-white);
}
.projects a h2:hover{
    color: var(--color-primary);
}
.projects h5{
    text-align: center;
}
.content_text{
    margin-top: 1rem;
   
}
.content_text h2{
    text-align: justify;
    color: var(--color-white);
    font-size: 0.8rem;
    font-weight: 500;
}
@media screen and (max-width:1024px) {
    section{
        height: 150%;
}
    .section_content{
    gap: 1rem;
    width: 100%;
}   
    .content_content{
        width: 100%;
        margin-left: 0;
        align-items: unset;
    }
    .aboutMySelf{
        justify-content: center;
        gap: 2rem;
    }
   
}
@media screen and (max-width:600px){
    section{
        height: 150%;
}
    .aboutMySelf{
        justify-content: center;
        gap: 0.4rem;
    }
.content_content{
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-left: 0;
}
.content_text h2{
    font-size: 15px;
}
.experience:hover{
    transform: unset;
 }
.projects:hover{
    transform: unset;
 }

}
@media screen and (max-width:400px){
    .aboutMySelf{
        gap: 0.5rem;
        flex-direction: column;
    }
    .content_text h2{
        font-size: 12px;
    }
    .content_content{
        margin-left: 0;
    }
  
   
}