.container_title{
    padding-top: 5rem;
}
.container_contacts{
    display: flex;
    flex-direction: row;
    gap: 5rem;
    justify-content: center;
    
}
.container_contacts .social_tab{
   display:flex;
   justify-content: center;
   padding: 0.5rem;
   flex-direction: column;
   gap: 0.5rem;
   text-align: center;
}
.social_tab .social{
    background: var(--color-bg-variant);
    padding: 1rem 3rem;
    border-radius: 20px;
    border: solid transparent;
    transition: var(--transition);
}
.social_tab .social:hover{
    background: transparent;
    border: solid var(--color-primary);
}
.social span{
    font-size: 1.2rem;
}
.social h4{
    font-size: 1.2rem;
}
.social h5{
    padding-bottom:0.4rem ;
    font-size: 0.8rem;
}

.container_contacts .btn{
    font-size: .7rem;
    padding: 0.3rem;
}
.container_contacts .contacts_tab{
    width: 50%;
}
.container_contacts .contacts_tab form{
    padding: 1rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 2rem;
    width: 100%;
    
}
.contacts_tab input{
    background: transparent;
    border: solid var(--color-primary);
    height: 2rem;
    width: 100%;
    padding: 0.4rem;
    border-radius: 10px;
    color: var(--color-primary);
}
.contacts_tab textarea{
    background: transparent;
    border: solid var(--color-primary);
    width: 100%;
    color: var(--color-white);
    border-radius: 10px;
    padding: 1rem;
}
.contacts_tab .btn{
    font-size: 1rem;
    padding: 1rem;
}
@media screen and (max-width:1024px){
    .container_contacts{
        flex-direction: column;
    }
    .container_contacts .social_tab{
        flex-direction: row;
    }
    .container_contacts .contacts_tab{
    width: 100%;
    }
    .contacts_tab form{
        align-items: center;
        gap: 1rem;
    }
}
@media screen and (max-width:600px) {
    .container_contacts{
        flex-direction: column;
        gap: 2rem;
        align-items: center;
    }
    .container_contacts .social_tab{
        flex-direction: column;
        width: 70%;
        
    }
    .container_contacts .contacts_tab{
        width: 100%;
    }
}