.title_container{
    text-align: center;
    height: 30%;
    padding-top: 6rem;
}
.title_container h2{
    font-size: 2.5rem;
}
.container_experience{
    display: flex;
    flex-direction: row;
    justify-content:center ;
    gap: 2rem;
    height: 100%;
    padding: 1rem;
}
.frontend{
    background: var(--color-bg-variant);
    height: 60%;
    border: solid var(--color-primary-variant);
    padding: 1rem;
    width: 40%;
    border-radius: 15px;
}
.c{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.pbar-c{
    background: var(--color-light);
    width: 100%;
    height: 1rem;
    border-radius: 10px;
    margin: 0.3rem 0;
}
.pbar-c .bar-fill{
    background: #FFC300 ;
    width: 85%;
    height: 1rem;
    border-radius:10px ;
}
.html{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.pbar-html{
    background: var(--color-light);
    width: 100%;
    height: 1rem;
    border-radius: 10px;
    margin: 0.3rem 0;
}
.pbar-html .bar-fill{
    background:	#FF4500;
    width: 90%;
    height: 1rem;
    border-radius:10px ;
}
.css{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.pbar-css{
    background: var(--color-light);
    width: 100%;
    height: 1rem;
    border-radius: 10px;
    margin: 0.3rem 0;
}
.pbar-css .bar-fill{
    background:	#4169E1;
    width: 90%;
    height: 1rem;
    border-radius:10px ;
}
.bootstrap{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.pbar-bootstrap{
    background: var(--color-light);
    width: 100%;
    height: 1rem;
    border-radius: 10px;
    margin: 0.3rem 0;
}
.pbar-bootstrap .bar-fill{
    background: #FF00FF;
    width: 85%;
    height: 1rem;
    border-radius:10px ;
}
.react{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.pbar-react{
    background: var(--color-light);
    width: 100%;
    height: 1rem;
    border-radius: 10px;
    margin: 0.3rem 0;
}
.pbar-react .bar-fill{
    background:#00BFFF;
    width: 70%;
    height: 1rem;
    border-radius:10px ;
}
.js{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.pbar-js{
    background: var(--color-light);
    width: 100%;
    height: 1rem;
    border-radius: 10px;
    margin: 0.3rem 0;
}
.pbar-js .bar-fill{
    background:	#FF0000;
    width: 75%;
    height: 1rem;
    border-radius:10px ;
}
@media screen and (max-width:600px){
    .container_experience{
        flex-direction: column;
    }
    .frontend{
        width: 100%;
    }
    .backend{
        width: 100%;
    }
}
